import React, { useState, useEffect, useRef } from 'react';
import {
  Card,
  CardBody,
  Col,
  Row,
  Container,
  CardHeader,
  CardFooter,
} from 'reactstrap';

import Footer from '../../components/Footer';
import Navbar from '../../components/Navbar';
import Notification from '../../components/Notification';
import ReactPlayer from '../../components/ReactPlayer';
import api from '../../services/api';

const Depoimentos = () => {
  const notificationRef = useRef();
  const [depoimentos, setDepoimentos] = useState([]);

  useEffect(() => {
    (async () => {
      try {
        const { data } = await api.get('/depoimentos');
        setDepoimentos(data);
      } catch (error) {
        console.log(error);
      }
    })();
  }, []);

  return (
    <Container fluid>
      <Notification ref={notificationRef} />
      <Navbar />
      <Card>
        <CardHeader>Depoimentos</CardHeader>
        <CardBody>
          <Row>
            {depoimentos?.map((depoimento) => (
              <Col md={6} key={depoimento._id}>
                <Card>
                  <CardHeader>{depoimento.name}</CardHeader>
                  <CardBody>
                    {depoimento.url ? (
                      <ReactPlayer url={depoimento.url} />
                    ) : (
                      <img src={depoimento.image_url} alt={depoimento.image} />
                    )}
                  </CardBody>
                  <CardFooter>{depoimento.mensagem}</CardFooter>
                </Card>
              </Col>
            ))}
          </Row>
        </CardBody>
      </Card>
      <Footer />
    </Container>
  );
};

export default Depoimentos;
